@forward 'slider';
html{
    box-sizing: border-box;
    font-size: 100%;
}
*,*::before,*::after{
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}
$Light: #ffecd6;
$Light2:#ffd4a3;
$Orange: #ffaa5e;
$MedDark: #203c56;

body{
    font-family: 'Poppins', sans-serif;
  
    .App{
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: $Light;

        .container{
            display: flex;
            flex-direction: column;
            padding: 20px;
            margin-top: 100px;
           
    
            .Main{
                max-width: 700px;
                text-align: center;
                color: $MedDark;
                background-color: $Light2;
                border-radius: 10px;
    
                .quote > p {
                    font-size: 20px;
                    line-height: 30px;
                    padding: 20px;
                    padding-bottom: 10px;
                    font-weight: 700;
                }
                .author{
                    padding-bottom: 20px;   
                }
            }
        }
      
        .button{
            align-self: center;
            padding: 10px;
            width: 150px;
            margin-top: 20px;
            border: none;
            cursor: pointer;
            border-radius: 5px;
            
        }
        .randomizer{
            background-color: $Orange;
            color: $MedDark;
            transition: 200ms;
            font-weight: 700;
            &:hover{
                transform: scale(1.05);
            }
        }
        
    }
  
}
